// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import iView from "iview";
import "iview/dist/styles/iview.css";

Vue.use(iView);
Vue.config.productionTip = false;

Vue.prototype.$setting = null;
Vue.prototype.broadcast = new Vue(); // 廣播用元件
Vue.prototype.$fetch = (sql) => {
  let TABLENAME = "GET_SQL";
  let s1 = sql.toUpperCase().trim();
  if ( s1.indexOf("INSERT ") === 0 || s1.indexOf("DELETE ") === 0 || s1.indexOf("UPDATE ") === 0) {
    TABLENAME = "SET_SQL";
  }
  // let json1 = {
  //   "URL": "https://rd.jabezpos.com/soap/B1POS/POS01013_SOAP.DLL/",
  //   "SITE": "BSMS000032",
  //   "USERID": "ADMIN@BSMS000032",
  //   "PASSWORD": "12345"
  // };
  // let json2 = {}
  // for(let key in json1) {
  //   json2[key] = btoa(json1[key]);
  // }
  // console.log(JSON.stringify(json2, null, 2))

  if (this.$setting == null) {
    if (typeof localStorage["PROJECT-setting"] === "string") {
      let json = JSON.parse(localStorage["PROJECT-setting"]);
      this.$setting = {};
      for(let key in json) {
        this.$setting[key] = atob(json[key]);
      }
    }
  }
  return new Promise(async (resolve, reject) => {
    if (typeof this.$setting.SITE === "undefined") {
      resolve("[]");
      return;
    }
    let url = this.$setting.URL +
      (TABLENAME === "SET_SQL" ? "SETDATA_IPAD" : "GETDATA_IPAD");
  
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/x-www-form-urlencoded");
    var urlencoded = new URLSearchParams();
    urlencoded.append("TABLENAME", TABLENAME);
    urlencoded.append("SITE", this.$setting.SITE);
    urlencoded.append("USERID", this.$setting.USERID);
    urlencoded.append("PASSWORD", this.$setting.PASSWORD);
    if (TABLENAME === "GET_SQL") {
      urlencoded.append("KeyFilterStr", sql);
      urlencoded.append("CAPACITY", "1000");
      console.log(sql);
    } else {
      urlencoded.append("DATAFILE", sql);
    }
    urlencoded.append("SHOWDEBUG", "TRUE");
    var requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: urlencoded,
      redirect: "follow"
    };
    console.log(url);
    fetch(url, requestOptions)
      .then((response) => response.text())
      .then((result) => {
        resolve(result);
      })
      .catch((error) => {
        console.log(error);
        reject(error);
      });
  });
};

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  async created() {
  },
})



