<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <div ref="dropbox" v-if="isLog == false" style="height: inherit;"></div>
    <Report v-else/>
  </div>
</template>

<script>
// import Project from './components/Project';
import Report from './components/Report';

export default {
  name: 'App',
  components: {
    Report,
    // Project
  },
  data () {
    return {
      isLog: false
    }
  },
  created() {
		// iview.lang('zh-TW');
  },
  async mounted(){
    if (typeof localStorage["PROJECT-USER"] === "string") {
      document.body.style.visibility = "visible";
      window.onresize = () => {
        return (() => {
          this.broadcast.$emit('on-resize');
        })()
      }
      this.isLog = true;
    } else {
      window.ondragover = (e) => {
        e.preventDefault();
        e.stopPropagation();
      };
      window.ondrop = (e) => {
        e.preventDefault();
        e.stopPropagation();
        if (e.dataTransfer.items) {
          const data = e.dataTransfer.items;
          for (var i = 0; i < data.length; i++) {
            if (data[i].kind === "file") {
              var file = data[i].getAsFile();
              if(file.name.indexOf(".json") > -1) {
                localStorage["PROJECT-USER"] = file.name.replace(".json", "")
                  let reader = new FileReader();
                  reader.onload = (event) => {
                    console.log(event.target.result);
                    setTimeout(() => {
                      localStorage["PROJECT-setting"] = event.target.result;
                      location.reload();
                    }, 600);
                  };
                  reader.readAsText(file);
                }
            }
          }
        }
        // this.broadcast.$emit("ondrop", e);
      };      
    }
  },
  methods: {
  }
}
</script>

<style>
* {
  box-sizing: border-box;
}
body {
  margin: 0px;
  background-color: #ebecf0;
  padding: 1px 2px;
  /* visibility: hidden; */
  /* color: rgb(0, 0, 0); */
  font-size: 16px;
}
html,
body,
#app {
  min-height: 100% !important;
  height: 100%;
}
</style>



