<template>
  <div style="height: 100%; display: flex; flex-direction: column; justify-content: center; align-items: stretch; padding: 5px;">
    <div style="margin: 5px 0;">
      <Select v-model="project" style="width:150px" @on-change="onChange" size="large">
        <Option v-for="item in projects" :value="item.PRJ_CODE" :key="item.PRJ_NAME">{{ item.PRJ_NAME }}</Option>
      </Select>      
    </div>
    <div ref="frame" style="flex: 1;">
      <Table :height="height" highlight-row border stripe :columns="columns" :data="datas" >
      </Table>
    </div>
  </div>
</template>

<script>
//https://www.iviewui.com/view-ui-plus/component/form/select
export default {
  name: 'Report',
  data () {
    return {
      project: "",
      projects: [],
      columns: [],
      datas: [],
      height: 300
    }
  },
  created() {
  },
  async mounted(){
    this.$Spin.show();
    this.onResize();

    this.broadcast.$on("on-resize", this.onResize);
    // window.addEventListener("keydown", this.onKeydown, false);
    if (typeof localStorage["PROJECT-KEY"] === "string") {
      this.project = localStorage["PROJECT-KEY"];
    }
    try {
      let sql = `select * from jim.PROJECT 
        where DATE_END = '' and
        (
          PM like '%${localStorage["PROJECT-USER"]}%'
          or RD like '%${localStorage["PROJECT-USER"]}%'
          or QA like '%${localStorage["PROJECT-USER"]}%'
        )
        order by PRJ_CODE`;
      let result = await this.$fetch(sql);
      // console.log(sql)
      // console.log(result);
      result = JSON.parse(result);
      if (typeof result.Data === "object") {
        this.projects = result.Data;
      }
      if(this.projects.length > 0) this.retrieve();
    } catch (e) {
      console.log(e);
    } finally {
      setTimeout(() => {
        this.$Spin.hide();
      }, 600);      
    }
  },
  destroyed() {
    this.broadcast.$off("on-resize", this.onResize);
    // window.removeEventListener("keydown", this.onKeydown, false);
  },
  methods: {
    onResize() {
      let frame = this.$refs["frame"];
      this.height = frame.clientHeight;
    },

    async retrieve() {
      let retrieveColumn = () => {
        this.columns = [
          {
            type: "index",
            align: "center",
            fixed: "left",
            width: 50,
            className: "index",
            indexMethod: (row) => {
              return row._index + 1;
            },
          }
        ];
        
        if(this.project.trim() == "") {
          this.columns.push(
          {
            title: "專案",
            key: "PRJ_NAME",
            width: 150,
            resizable: true,
            fixed: "left",
          })
        }

        this.columns = this.columns.concat([{
          title: "標題",
          key: "TITLE",
          resizable: true,
          width: 200,
          // fixed: "left",
          tooltip: true,
        }, {
          title: "說明",
          key: "MEMO",
          resizable: true,
          tooltip: true,
        }, {
          title: "狀態",
          key: "STATUS",
          resizable: true,
          width: 50,
          // tooltip: true,
        }, {
          title: "更新者",
          key: "USER_UPDATE",
          resizable: true,
          // tooltip: true,
          width: 100,
        }
      ]);
      }
      retrieveColumn();

      this.$Spin.show();
      try {
        let sql = `select a.* 
          from jim.REPORT as a
          where a.PRJ_CODE = '${this.report} order by a.PK desc'`;
        let result = await this.$fetch(sql);
        // console.log(sql)
        // console.log(result);
        result = JSON.parse(result);
        if (typeof result.Data === "object") {
          this.projects = result.Data;
        }
      } catch (e) {
        console.log(e);
      } finally {
        setTimeout(() => {
          this.$Spin.hide();
        }, 600);      
      }
    },
    onChange(e) {
      console.log(this.project)
      localStorage["PROJECT-KEY"] = this.project;
      this.retrieve();
    }
  }
}
</script>
<style scoped>
</style>



